.consultation{
    min-height: 680px;
    padding: 100px 0 92px 0;
    background: url(../../img/consultation.jpg) center (center/cover) no-repeat;
    &__title{
        font-size: 36px;
        font-weight: bold;
        text-align: left;
        color: #ffffff;
    }
    &__descr{
        margin-top: 80px;
        font-size: 20px;
        font-weight: 500;
        text-align: left;
        color: #ffffff;
    }
    .error {
        border: 1px solid red;
    }
    label.error {
        color: #fff;
        border: none;
        text-align: center;
        margin-bottom: 15px;
    }
}